.landing {
  min-height: 100vh;
  background-size: cover;
  background-position: bottom;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  color: #333;
  text-align: center;
  padding: 2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.landing h1 {
  font-size: 3rem;
  margin: auto auto 0 auto;
  margin-top: calc(50vh - 7rem);
}
.landing h4 {
  margin: 0 auto;
}
.landing a {
  margin: 5rem auto auto auto;
  text-decoration: none;
  color: #fff;
  padding: 0.75rem 1.5rem;
  border-radius: 1.5rem / 50%;
  background-color: #333;
}
.landing a:hover {
  background-color: #555;
}
.landing img {
  background-color: transparent;
  border: none;
  outline: none;
  margin: 0 auto 2rem auto;
  width: 1.5rem;
  opacity: 0.6;
  cursor: pointer;
}
.landing img:hover {
  opacity: 0.8;
}
.home-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: var(--xl-space) 0;
}
.home-section h1, .home-section p {
  margin-top: 0;
}
.home-section > div {
  white-space: nowrap;
  padding: var(--m-space);
  -ms-flex: 1 1;
      flex: 1 1;
}
.store-hours > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.store-hours > div > p:first-child {
  margin-right: var(--s-space);
}
@media screen and (min-width: 768px) {
  .home-section > div {
    max-width: 320px;
  }
}


